.responsive-image-container {
    position: relative;
    width: 100%;
    padding-top: 100%; /* Controls the aspect ratio of the image (square in this case) */
  }
  
  .responsive-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /* Maintain aspect ratio and cover the container */
  }
  